.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  margin: 60px 0px 60px 0px;
  line-height: normal;
  text-align: center;
  @media screen and (max-width: 767px){
    margin: 40px 0px 40px 0px;
    
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 13px 13px 11px 13px;
  margin: 0;
  color: #000000;
  font-family: 500;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  border-radius: 5px;
  vertical-align: middle;

  @media screen and (max-width: 767px) {
    display: inline;
    padding: 10px 10px 10px 10px;
  }
}

.tui-pagination .tui-page-btn:hover {
  background-color: #f4f4f4e0;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: white;
  background: #B92F2C;
  border-radius: 5px;
  cursor: pointer;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #1e0fec;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  width: 40px;
  height: 40px;
  padding: 13px 13px 11px 13px;
  font-size: 12px;
  line-height: 16px;
  white-space: nowrap;
  font-weight: bold;
  background-color: #f7f7f7
}


.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
  background-color: #f7f7f7;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}


.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 8px;
  height: 12px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 8px;
  height: 12px;
  color: white;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 19px;
  vertical-align: top;
  background-position: 0px -24px;
}
@media screen and (max-width: 767px) {
  .tui-pagination .tui-ico-ellip{
    display: none;
  }
}

.tui-pagination .tui-is-disabled:hover {
  background: #f7f7f7;
  cursor: pointer;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: black;
  border: 0;
  outline: none;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.pagination {
  margin: 0;
}