body.show-modal .backdrop {
    opacity: 4;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    visibility: visible;
    pointer-events: initial;
}

.backdrop {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 4;
    background-color: rgba(0, 0, 0, 0.2);
    
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    overflow-y: scroll;
    padding: 250px 237px;

}

.modal {
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    background-color:rgba(255, 255, 255, 1);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14),
        0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    min-width: 806px;
    padding: 40px 12px;
}

.modal-card {
    display: flex;  
}

.modal-img { @media screen and (min-width: 1280px) {
        width: 375px;  
        height: 478px;
        margin-right: 16px;
   }
}

.modal-info {
    font-weight: 500;
    font-size: 12px;
    color: rgba(0, 0, 0, 1);
}

.modal-item {
    color: rgba(140, 140, 140, 1);
    margin-right: 20px;
}

.title {
    font-size: 30px; 
    line-height: 1.17;
    margin-bottom: 20px;
}

.modal-item-list{
    margin-bottom: 8px;
    display: flex;
}

.catalog {
    margin-bottom: 20px;
}

.modal-about {
    margin-bottom: 8px;
}

.overview {
    margin-bottom: 20px;
}

.modal-btn {
    display: flex;
    height: 42px;
}

.add-watched {
    min-width: 165px;
    margin-right: 8px;
    background-color: rgba(185, 47, 44, 1);
    color: rgba(255, 255, 255, 1);
}

.add-queue {
    min-width: 134px;
    
}

